import React, { useEffect, useState } from 'react'
import { useLocation } from 'react-router-dom'
import H2 from '../H2/H2'
import ContactsCompMap from './ContactsCompMap'

export default function S9Contacts() {
  const leftItem = [
    {city: '', adress: <>. . , 123 <br/>
    . (495) 123-456-78, 564-88-99 <br/>
    E-mail: alfamed@alfamed.ru</> },
    {city: '- ', adress: <>. . , 123 <br/>
    . (495) 123-456-78, 564-88-99 <br/>
    E-mail: alfamed@alfamed.ru</> },
    {city: '', adress: <>. . , 123 <br/>
    . (495) 123-456-78, 564-88-99 <br/>
    E-mail: alfamed@alfamed.ru</> },
    {city: '', adress: <>. . , 123 <br/>
    . (495) 123-456-78, 564-88-99 <br/>
    E-mail: alfamed@alfamed.ru</> },

  ]
  const location = useLocation();
useEffect(() => {
  //      . 
  setButttonValue('  ') 
  setAddClass( 'h9__button btn')
 }, [location]); //    Location

  const [values, setValues] = useState(  
    {
      name: '',  
      email: '',
      text: ''
   }
  )
   //   
   const [ buttonValue, setButttonValue] = useState('  ')
const [ submited, setSubmited] = useState(false)

  function handleName(e) { 
    setValues({...values, name: e.target.value })
 
    }
  function handleEmail(e) { 
    setValues({...values, email: e.target.value })
  }
  function handleText(e) { 
  setValues({...values, text: e.target.value })
  }
    //  
    const [addClass, setAddClass] = useState('h9__button btn')

  function handleSubmit(e) { 
    e.preventDefault()
    if(values.name && values.email && values.text) {
      setSubmited(true) //  
      setButttonValue(' ')
      setAddClass( 'h9__button btn')
 
setTimeout(() => {
  setValues({
    ...values,
    name: '',
    email: '',
    text: '', 
  })
  // setSubmited(false) //           
  setButttonValue('  ') //    
}, 3000);

    } else {
      setButttonValue('  ')
      setAddClass( 'h9__button warn-btn btn')
    }
  
  }

   
  return (
   <>
    <section className="home-9">

 

<div className="container">
    <H2
subTitle={' '}
h2Title={<> <span> </span>  </>}
text={''}
subTitleStyles={'h9__smallTitle'}
h2TitleStyles={'h9__title'}
textStyles={''}
/>
      
      <div className="h9__row">
        <div className="h9-row__left">
          {
            leftItem.map((item, i)=> { 
             return ( 
              <div key={i} className="h9-left__item">
            <div className="h9-item__title">{item.city}</div>
            <h3 className="h9-item__adress">{item.adress}</h3>
          </div>
              )})  
          }
          </div>
        <div className="h9-row__right">
        
          <form onSubmit={handleSubmit} action="#" className='form-sec9'  data-contacts>
            {submited ? <div className='form-title'> </div> : <div className='form-title' > </div> }

            <input
            onChange={handleName}
            value={values.name}
            type="text" className="h9__input" placeholder=" "/>
            <div className="form-span">
            {!values.name ? <span> *</span> : null}
            </div>
         

            <input 
              onChange={handleEmail}
            value={values.email}
            type="text" className="h9__input" placeholder=" "/>
            <div className="form-span">
            {!values.email ? <span> Email*</span> : null}
            </div>
            <textarea
               onChange={handleText}
               value={values.text}
            name="#" className="h9__textarea" placeholder=" "></textarea>
            <div className="form-span">
            {!values.text ? <span> *</span> : null}
            </div>
            <button type="submit" className={addClass}>{buttonValue}</button>
          </form>
        </div>
      </div>
    </div>
  </section>
  {location.pathname === '/contacts' ? <ContactsCompMap/> : null }
   </>
  )
}
